































import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import { Editor, EditorContent, EditorMenuBar } from 'tiptap'
import { EditorState } from 'prosemirror-state'
import { v4 as uuid } from 'uuid'
import {
  Blockquote,
  HardBreak,
  Heading,
  OrderedList,
  BulletList,
  ListItem,
  Bold,
  Italic,
  History,
  Focus
} from 'tiptap-extensions'
import Realtime from '../editor/realtime'
import Comments from '../editor/comments'
import { commentStore, commentMutations, CommentThread } from '../editor/commentStore'
import { findChildrenByMark } from 'prosemirror-utils'
import _ from 'lodash'
import eventBus from '../eventBus'
import user from '../store/user'

@Component({
  components: {
    EditorContent,
    EditorMenuBar
  }
})
export default class MEditor extends Vue {

  json = ''

  comments = commentStore

  editor = new Editor({
    extensions: [
      new Blockquote(),
      new HardBreak(),
      new Heading({ levels: [1, 2, 3] }),
      new BulletList(),
      new OrderedList(),
      new ListItem(),
      new Bold(),
      new Italic(),
      new History(),
      new Comments(),
      new Realtime({
        user: user.profile,
        onUpdateUsers(us) {
          // console.log(this)
          // console.log(us)
        },
        onInit: async (us) => {
          // await this.$nextTick()
          // if (us.length === 1) {
          //   console.log('only one user.')
          // }
        }
      }),
      new Focus()
    ],
    onTransaction: ({ state }: { state: EditorState }) => {
      const $pos = state.doc.resolve(state.selection.$from.pos)
      const parent = $pos.parent
      const start = parent.childAfter($pos.parentOffset)
      const thread = start.node?.marks.find(mark => mark.type.name === 'comments')?.attrs
      eventBus.$emit('selectThread', thread)
    },
    onUpdate: ({ state }: { state: EditorState }) => {
      const nodes = findChildrenByMark(state.doc, state.schema.marks.comments)
      const comments = _(nodes).map((n) => {
        return n.node.marks.find(m => m.type.name === 'comments')?.attrs as CommentThread
      }).uniqBy('threadId').value()
      commentMutations.updateCommentsFromDoc(comments)
    }
  })

  mounted() {
    eventBus.$on('appendComment', ({ threadId, comment }: {threadId: string, comment: any}) => {
      this.editor.commands.appendComment({threadId, comment})
    })
  }

  addComment() {
    const comment = prompt('comment:')
    if (comment) {
      this.editor.commands.startThread({
        threadId: uuid(),
        state: 'open', // 'open' or 'resolved'
        thread: [{
          commentId: uuid(),
          user: user.profile.username,
          text: comment,
          time: new Date(),
          state: 'composing' // composing, saved
        }]
      })
    }
  }
}
