import { Mark as MarkPlugin } from 'tiptap'
import { DispatchFn } from 'tiptap-commands'
import { MarkSpec, MarkType } from 'prosemirror-model'
import { findChildrenByMark, NodeWithPos } from 'prosemirror-utils'
import { EditorState } from 'prosemirror-state'

export default class Comments extends MarkPlugin {

  get defaultOptions() {
    return {
      user: null
    }
  }

  get name() {
    return 'comments'
  }

  get schema() {
    return {
      attrs: {
        thread: {
          default: [{
            user: 'arni',
            time: new Date(),
            text: 'comment'
          }]
        },
        threadId: {},
      },
      content: 'inline*',
      group: 'block',
      draggable: false,
      parseDOM: [{
        tag: 'comment',
        getAttrs(dom: any) {
          return {
            threadId: dom.dataset.threadId,
            thread: JSON.parse(dom.dataset.thread)
          }
        }
      }],
      toDOM(node: any) {
        return {
          0: 'comment',
          1: {
            'data-thread-id': node.attrs.threadId,
            'data-thread': JSON.stringify(node.attrs.thread)
          },
          2: 0 as 0
        }
      }
    }
  }

  startThread(type: MarkType, attrs: any) {
    return (state: EditorState, dispatch: DispatchFn) => {
      dispatch(state.tr.addMark(state.selection.from, state.selection.to, type.create(attrs)))
      return true
    }
  }

  findThreadNodesById(id: string, state: EditorState): NodeWithPos[] {
    const nodes = findChildrenByMark(state.doc, state.schema.marks.comments)
    const nodesWithComments = nodes.filter(n => {
      return n.node.marks.find(m => m.attrs.threadId === id) !== undefined
    })
    return nodesWithComments
  }

  appendComment(type: MarkType, threadId: string, comment: any) {
    return (state: EditorState, dispatch: DispatchFn) => {
      const nodes = this.findThreadNodesById(threadId, state)
      nodes.forEach(n => {
        const oldAttrs = n.node.marks.find(n => n.type.name === 'comments')?.attrs
        const newAttrs = {
          ...oldAttrs,
          thread: [
            ...oldAttrs?.thread,
            comment
          ]
        }
        dispatch(
          state.tr
            .removeMark(n.pos, n.pos + n.node.nodeSize, type)
            .addMark(n.pos, n.pos + n.node.nodeSize, type.create(newAttrs))
        )
      })
      return true
    }
  }

  commands({type, schema} : { type: MarkType, schema: MarkSpec }) {
    return {
      appendComment: ({threadId, comment}: {threadId: string, comment: any}) => {
        return this.appendComment(type, threadId, comment)
      },
      startThread: (attrs: any) => this.startThread(type, attrs)
    }
  }
}
