








































import { Vue, Component, Prop, Watch } from 'vue-property-decorator'
import EventBus from '../eventBus'
import { v4 as uuid } from 'uuid'
import user from '../store/user'

@Component
export default class CommentThread extends Vue {
  @Prop() thread: any

  isSelectedThread = false
  currentAnswer = ''

  appendComment() {
    if (this.currentAnswer.trim() !== '') {
      const comment = {
        commentId: uuid(),
        state: 'composing',
        user: user.profile.username,
        time: new Date(),
        text: this.currentAnswer
      }
      this.currentAnswer = ''
      EventBus.$emit('appendComment', {threadId: this.thread.threadId, comment})
    }
  }

  mounted() {
    EventBus.$on('selectThread', (thread: any) => {
      if (thread && thread.threadId === this.thread.threadId) {
        this.isSelectedThread = true
      } else {
        this.isSelectedThread = false
      }
    })
  }
}
